@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.header {
  display: flex;
  justify-content: space-between;
  place-items: center;
  flex-direction: row;
  border-bottom: 1px solid gray;
}

.btn {
  color: white;
  border: none;
  border-radius: 5px;
  background-color: rgb(68, 68, 68);
  min-width: 3rem;
  font-weight: bold;

  cursor: pointer;
}

.add {
  margin: 10px 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.add > input {
  width: 70%;
  padding: 5px 10px;
  font-size: 1.3rem;
}

.tasks {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  place-items: left;
}

.task {
  padding: 5px 10px;
  margin: 10px 0;
  background-color: rgb(227, 235, 241);
  border-radius: 5px;
}

.task h3 {
  display: flex;
  justify-content: space-between;
  place-items: center;
}

.tasks input {
  transform: scale(1.2);
}

.imp {
  border-bottom: 5px solid limegreen;
  box-sizing: content-box;
}

.noteToast{
  top: 10vh;
  display: block;
  left: 15%;
  position: fixed;
  background-color: white;
  width: 70%;
  border: 2px solid black;
  border-radius: 10px;
  height: 70vh;

  padding: 20px;
}

.noteToast textarea{
  margin-top: 5px;
  width: 100%;
  overflow: visible;
  height: 55vh;
  resize: none;
  border: none !important;
  outline: none;

  font-size: 1rem;
}

.footer{
  position: fixed;
  bottom: 0;
  right: 5px;
}
.footer  p {
  color: gray;
}

@media (max-width: 600px) {
  .container{
    border: none;
  }
}